/*Reset*/
* {
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
a, img, button, input {
  display: block;
}
a, img, button {
  cursor: pointer;
}
button {
  background-color: transparent;
  border: none;
}
a {
  text-decoration: none;
}
button:focus, input:focus {
  outline: none;
}
ul {
  list-style-type: none;
}

/*Main settings*/

html {
  font-size: 10px;
}
#root {
  width: 100%;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  overflow: hidden;
}
.wrap {
  width: 100%;
  height: 100vh;
  background-color: #F5F6F8;
  display: grid;
  grid-template-columns: 1fr 4.625fr;
}
.wrapper {
  position: relative;
  width: auto;
  height: 100vh;
  overflow: auto;
}
.row {
  display: flex;
}
.component__wrapper {
  padding: 2.88% 4.07% 2.88% 2.72%;
}
.component__title {
  display: inline-block;
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 1;
  letter-spacing: 0.02em;
  color: #000000;
  padding-bottom: 1.2rem;
  border-bottom: 1px solid #3E8CE2;
}
.title__row {
  display: flex;
  align-items: center;
}
.text__link {
  display: inline;
  color: #3E8CE2;
  font-size: inherit;
}
.input__block {
  width: 100%;
  position: relative;
}
.input__block input{
  width: 100%;
}
::-webkit-scrollbar {
  width: 0.4rem;
  height: 100%;
}
::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 0.2rem;
}
::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.8);
}
.icon-arrow_2svg {
  font-size: 6px;
}
@media screen and (max-width: 767px){
  .wrap {
    grid-template-columns: 1fr 10.625fr;
  }
}
@media screen and (max-width: 575px){
  .wrap {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .wrapper {
    width: auto;
    height: calc(100vh - 8rem);
    overflow-y: scroll;
  }
}

.menu {
    z-index: 2;
    width: 100%;
    padding-top: 2.6rem;
    background: linear-gradient(180deg, #5C83D0 0%, #518CDE 100%);
    box-shadow: 6px 0px 18px rgba(0, 0, 0, 0.06);
    height: 100%;
}
.logo {
    width: 82.42%;
    margin-left: 8.2%;
}
.logo__min {
    display: none;
}
.menu__user__block {
    display: flex;
    align-items: center;
    width: 81.25%;
    margin: 8.59% auto 14.84%;
}
.user__icon__block {
    position: relative;
    width: 22.11%;
    margin-right: 7.69%;
    padding-top: 22.11%;
    border-radius: 50%;
}
.user__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.user__name {
    font-size: 1.6rem;
    line-height: 1.9rem;
    color: #FFFFFF;
}
.user__email {
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 1.7rem;
    letter-spacing: 0.01em;
    color: #FFFFFF;
}
.menu__link {
    margin: 7.1% 0 7.1% 14.84%;
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    line-height: 1.7rem;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    opacity: .5;
    transition: all 0.3s ease;
}
.menu__link.active {
    opacity: 1;
}
.menu__link:hover {
    opacity: 1;
}
.link__icon {
    margin-right: 1.3rem;
    font-size: 2rem;
}
.plus__icon {
    margin-top: -3px;
}
.menu__border__div {
    width: 100%;
    height: 1px;
    background-color: rgba(235, 239, 242, 0.5);
    margin: 16.4% 0 9.375%;
}
.sublinks__block {
    height: 0;
    width: 100%;
    margin-left: 11.72%;
}
.sublinks__block .menu__link {
    display: none;
}
.sublinks__block.active {
    height: auto;
}
.sublinks__block.active .menu__link {
    display: flex;
}
.sublinks__block.s__sublinks__block {
    height: 0;
}
.sublinks__block.s__sublinks__block .menu__link {
    display: none;
}
.sublinks__block.s__sublinks__block.active {
    height: auto;
}
.sublinks__block.s__sublinks__block.active .menu__link{
    display: flex;
}
.sublink__icon {
    font-size: 1.4rem;
}
.menu__link:nth-child(2) {
    margin-bottom: 0;
}
@media screen and (max-width: 1440px) {
    .menu {
        width: 25.6rem;
    }
}
@media screen and (max-width: 1100px) {
    .menu {
        width: 22rem;
    }
}
@media screen and (max-width: 767px) {
    .logo {
        display: none;
    }
    .logo__min {
        display: block;
        width: 4rem;
        margin-bottom: 3rem;
    }
    .menu {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .menu__user__block {
        display: flex;
        align-items: center;
        width: 100%;
        margin: 0;
    }
    .user__icon__block {
        width: 100%;
        padding-top: 100%;
        margin-right: 0;
    }
    .user__name {
        display: none;
    }
    .user__email {
        display: none;
    }
    .menu__block {
        width: 100%;
        position: relative;
        z-index: 3;
    }
    .sublinks__block {
        position: absolute;
        right: 0;
        transform: translateX(-200%);
        top: 62px;
        width: 200px;
        height: auto;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background: linear-gradient(180deg, #5C83D0 0%, #518CDE 100%);
        transition: all 0.3s ease;
        z-index: 11;
    }
    /*.sublinks__block.active {
        transform: translateX(100%);
    }*/
    .sublinks__block.active__mobile .menu__link {
        display: flex;
    }
    .sublinks__block.active__mobile {
        transform: translateX(100%);
    }
    .sublinks__block.s__sublinks__block.active__mobile {
        height: auto;
    }
    .sublinks__block.s__sublinks__block.active__mobile .menu__link{
        display: flex;
    }
    .sublinks__block .link__title {
        display: block;
        margin-left: 1rem;
    }
    .sublinks__block .menu__link {
        width: calc(100% - 4rem);
        margin: 1.5rem 2rem 1.5rem;
    }
    .menu__link {
        margin: 3rem 0 0 0;
        justify-content: center;
    }
    .menu__border__div {
        margin:3rem 0 2rem;
    }
    .link__title {
        display: none;
    }
    .link__icon {
        margin-right: 0;
        font-size: 2.4rem;
    }
}
@media screen and (max-width: 575px) {
    .menu {
        width: 100%;
        padding: 0 4.07% 0 2.72%;
        flex-direction: row;
        justify-content: space-between;
        height: 8rem;
        overflow-y: hidden;
    }
    .menu__border__div {
        display: none;
    }
    .user__block, .menu__block {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .user__block {
        width: 30%;
    }
    .menu__block {
        width: 30%;
        position: static;
    }
    .logo__min {
        margin-bottom: 0;
    }
    .user__icon__block {
        width: 4rem;
        padding-top: 4rem;
        margin-right: 0;
        margin-left: 3rem;
    }
    .menu__link {
        margin: 0;
    }
    .sublinks__block {
        width: 0%;
        padding: 0;
        top: 8rem;
        right: 0;
        transform: translateX(0%);
    }
    .sublinks__block .menu__link {
        display: none;
    }
    .sublinks__block.active__mobile {
        padding: 1% 5%;
        width: 50%;
        transform: translateX(0%);
    }
    .sublinks__block.active .menu__link {
        display: flex;
    }
}
@media screen and (max-width: 400px) {
    .menu__block {
        width: 45%;
    }
}
